:root {
  --FONT-FAMILY: 'Roboto', sans-serif;
  --RED: #ff001b;
  --ORANGE: #ff6b08;
  --WHITE: #fff;
}
body {
  font-family: var(--FONT-FAMILY);
  font-weight: 500;
  font-size: 12px;
  color: var(--WHITE);
}

.MainHeader {
  background-image: linear-gradient(#0000008f, #0000008f), url('../images/library_mobile.jpg');
  background-repeat: round;
  background-size: cover;
  height: 230px;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
@media only screen and (min-device-pixel-ratio: 2), only screen and (min-resolution: 192dpi) {
  .SectionHeader {
    background-image: url('../images/library_mobile@2x.jpg');
  }
}

.Navigation {
  padding: 40px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.LogoWraper {
  display: flex;
  cursor: pointer;
}
.ImgCenter {
  display: flex;
  align-items: center;
}
.LogoTitle {
  display: none;
}

.List {
  display: flex;
  gap: 40px;
  list-style-type: none;
  padding-inline-start: 0px;
  margin-block-start: 0em;
  margin-block-end: 0em;
}
.Link {
  text-decoration: none;
  line-height: 14.06px;
  text-transform: uppercase;
  text-align: center;
  color: var(--WHITE);
  position: relative;
}
.Link::after {
  content: '';
  background-color: var(--RED);

  right: 0;
  bottom: -4px;

  height: 3px;
  width: 0;

  position: absolute;
  transition: width 300ms, left 300ms;
}
.Link:hover::after {
  width: 100%;
  left: 0;
}

.Current::after {
  content: '';
  background-color: var(--RED);
  position: absolute;
  left: 0;
  bottom: -4px;
  width: 100%;
  height: 3px;
}

.ButtonsWrapper {
  display: flex;
  justify-content: center;
  gap: 20px;
  padding: 20px;
  margin-bottom: 42px;
}
.Buttons {
  background: transparent;
  border: 1px solid var(--WHITE);
  border-radius: 5px;
  width: 129px;
  height: 44px;

  font-weight: 500;
  text-transform: uppercase;
  text-align: center;
  color: var(--WHITE);
}
.Buttons:hover {
  border-color: var(--ORANGE);
  background: var(--ORANGE);
  box-shadow: 0px 8px 43px 0px #ff6b0199;
  cursor: pointer;
}
.SelectedButton {
  border-color: var(--ORANGE);
  background: var(--ORANGE);
  box-shadow: 0px 8px 43px 0px #ff6b0199;
}

.LibraryContener {
  padding: 20px;
}
.DemoCard {
  width: 280px;
  height: 438px;
  background-color: aliceblue;
}

@media screen and (min-width: 400px) {
  .MainHeader {
    background-repeat: no-repeat;
    background-image: linear-gradient(#0000008f, #0000008f), url('../images/library_tablet.jpg');
    height: 219px;
  }
  @media only screen and (min-device-pixel-ratio: 2), only screen and (min-resolution: 192dpi) {
    .SectionHeader {
      background-image: url('../images/library_tablet@2x.jpg');
    }
  }
  .Navigation {
    padding: 40px 32px;
  }
  .LogoWraper {
    gap: 10px;
  }
  .LogoTitle {
    font-weight: 500;
    font-size: 30px;
    color: var(--WHITE);
    display: inline;
    line-height: 35.16px;
  }
  .ButtonsWrapper {
    padding: 0;
    gap: 32px;
    margin-bottom: 60px;
  }
  .Buttons {
    width: 152px;
  }
  .footer {
    padding-top: 30px;
    padding-bottom: 28px;
  }
}
@media screen and (min-width: 769px) {
  .MainHeader {
    background-image: linear-gradient(#0000008f, #0000008f), url('../images/library_desktop.jpg');
    height: 216px;
    background-position: bottom;
  }
  .ButtonsWrapper {
    gap: 16px;
  }
  .Buttons {
    width: 147px;
    height: 42px;
  }
  .footer-text {
    font-size: 16px;
    line-height: 1.171;
  }
  .textfirst {
    display: inline-block;
  }
}
